#player-main .volume-control {
  display: flex;
  gap: 5px;
  align-items: center;
  /* Estilo geral do input range */
  /* Estilo do "thumb" (o bot√£o do slider) */
  /* Estilo do thumb no Firefox */
  /* Estilo da barra quando o slider √© movido */
}
#player-main .volume-control #volume-slider {
  -webkit-appearance: none; /* Remove o estilo padr√£o no Chrome/Safari */
  appearance: none; /* Remove o estilo padr√£o */
  width: 100%; /* Tamanho do slider */
  height: 5px; /* Altura da barra */
  background: linear-gradient(to right, #708303, #e9ff70);
  border-radius: 5px; /* Cantos arredondados */
  outline: none; /* Remove o contorno ao focar */
  transition: background 0.3s ease; /* Transi√ß√£o suave */
}
#player-main .volume-control #volume-slider::-webkit-slider-thumb {
  -webkit-appearance: none; /* Remove o estilo padr√£o no Chrome/Safari */
  appearance: none; /* Remove o estilo padr√£o */
  width: 15px; /* Largura do thumb */
  height: 15px; /* Altura do thumb */
  border-radius: 50%; /* Tornando o thumb redondo */
  background: #fff; /* Cor do thumb */
  cursor: pointer; /* Cursor de ponteiro */
}
#player-main .volume-control #volume-slider::-moz-range-thumb {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #333;
  cursor: pointer;
}
#player-main .volume-control #volume-slider:focus {
  background: #aaa; /* Altera a cor da barra quando o slider √© focado */
}
#player-main .volume-control #volume-slider:active {
  background: #888; /* Altera a cor da barra quando o slider √© clicado */
}

