@use './colors.scss';
@use './fonts.scss';

body {
    background-color: colors.$dark-main;
    color: colors.$text;
    font-family: "Poppins", sans-serif;
    font-size: 16px;
    -webkit-user-select: none; /* Safari */
    -moz-user-select: none;    /* Firefox */
    -ms-user-select: none;     /* IE/Edge */
    user-select: none;         /* Padr√£o */
    overflow: hidden;
}




.bottom-bar {
    height: 100px;
    background-color: colors.$dark-sidebar;
    border-top: 1px solid colors.$dark-borders;
}

.main-content {
    display: flex;
    height: calc(100vh - 180px);
    overflow: hidden;
}

.main {
    flex: 1;
    padding: 30px;
    overflow-y: scroll;
    height: calc(100vh - 180px);
}

.sidebar {
    width: 250px;
    background-color: colors.$dark-sidebar;
}