@use '../utils/colors.scss';

.playlist {
    margin-top: 40px;
}

.song {
    display: flex;
    height: 80px;
    padding: 10px;
    transition: 300ms;
    border-radius: 6px;
    align-items: center;
    gap: 10px;

    // se o som estiver ativo
    &.active {
        background-color: rgba(colors.$main-color, 0.05);

        &:hover {
            background-color: rgba(colors.$main-color, 0.05);
        }


        &:nth-child(odd) {
            background-color: rgba(colors.$main-color, 0.05);
        }

        .play-button {
            background-image: url('../img/icons/pause.svg');
        }

        .song-cover {
            .thumb {
                display: none;
            }
    
            .sound-wave {
                display: flex;
            }
        }

    }

    &:nth-child(odd) {
        background-color: colors.$dark-sidebar;
    }


    &:hover {
        background-color: rgba(colors.$main-color, 0.05);
    }

    .is-new {
        font-size: 10px;
        text-transform: uppercase;
        border: 1px solid #272727;
        border-radius: 4px;
        display: inline-block;
        height: 20px;
        width: 36px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: 1px;
        background-color: rgba(colors.$main-color, 0.05);
        color: colors.$main-color;
    }

    .song-cover {
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;

        .thumb {
            height: 60px;
            width: 60px;
        }

        .sound-wave {
            display: none;
        }
    }



    .title-artist {
        display: flex;
        flex-direction: column;
        width: 150px;
        overflow-x: hidden;
        height: 60px;
        justify-content: center;
        

        .title {
            font-size: 14px;
            color: #fff;
        }

        .artist {
            font-size: 14px;
            color: rgba(#fff, 0.5);

            &:hover {
                color: colors.$main-color;
            }
        }
    }

    .play-button {
        border: 0;
        background-image: url('../img/icons/play.svg');
        height: 30px;
        width: 30px;
        border-radius: 2px;
        background-color: colors.$dark-borders;
        background-repeat: no-repeat;
        background-position: center;
        cursor: pointer;
        background-size: 10px;
    }

    .time {
        color: rgba(#fff, 0.5);
        font-size: 14px;
    }

    .new-tag-spot {
        width: 36px;
    }

    .download-link {
        background-color: colors.$dark-borders;
        color: colors.$text;
        border: none;
        padding: 8px 20px;
        border-radius: 20px;
        font-size: 13px;
        cursor: pointer;
        transition: all 0.2s ease;

        &:hover {
            color: colors.$main-color;
            background-color: rgba(colors.$main-color, 0.05);
        }
    }

    select {
        background-color: #272727;
        border: 1px solid #272727;
        color: #e3e3e3;
        padding: 4px 4px;
        border-radius: 20px;
        font-size: 13px;
        cursor: pointer;
        transition: all .2sease;
        outline: 0;
        margin: 0;
        transform: translateY(-4px);

        &:hover {
            color: colors.$main-color;
            background-color: rgba(colors.$main-color, 0.05);
        }

        option {
            background-color: colors.$dark-sidebar;
            color: rgba(#fff, 0.5);

            &:hover {
                color: colors.$main-color;
                background-color: rgba(colors.$main-color, 0.05);
            }
        }
    }

    .add-to-playlist {
        background-color: #272727;
        border: 1px solid #272727;
        color: #e3e3e3;
        padding: 4px 6px;
        border-radius: 20px;
        font-size: 13px;
        cursor: pointer;
        transition: all .2sease;
        outline: 0;
        margin: 0;
        transform: translateY(-4px);

        &:hover {
            color: colors.$main-color;
            background-color: rgba(colors.$main-color, 0.05);
        }
    }

    .genders {
        display: flex;
        gap: 5px;

        li {
            font-size: 12px;
            color: rgba(colors.$main-color, 0.7);
            border-radius: 4px;
            background-color: rgba(#fff, 0.1);
            padding: 2px 4px;
            font-style: italic;
        }
    }
}